@import url(https://fonts.googleapis.com/css2?family=Alatsi&display=swap);
html,
body,
#root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
}

::-webkit-input-placeholder {
  color: white;
}

:-ms-input-placeholder {
  color: white;
}

::-ms-input-placeholder {
  color: white;
}

::placeholder {
  color: white;
}

:root {
  --animation-time: 2s;
}

.star {
  -webkit-animation: 2s infinite alternate pulse1;
          animation: 2s infinite alternate pulse1;
  -webkit-animation: var(--animation-time) infinite alternate pulse1;
          animation: var(--animation-time) infinite alternate pulse1;
}

@-webkit-keyframes pulse0 {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes pulse0 {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

