@import url('https://fonts.googleapis.com/css2?family=Alatsi&display=swap');

html,
body,
#root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
}

::placeholder {
  color: white;
}

:root {
  --animation-time: 2s;
}

.star {
  animation: var(--animation-time) infinite alternate pulse1;
}

@keyframes pulse0 {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
